.table {
  box-shadow: #000 0 0 3px;
  border-radius: 3px;
  padding: 20px;
}

.table h1 {
  color: #3a33d1;
}

.table * {
  background-image: url("../theme/icon.svg");
}

.table caption {
  font-size: 1.2em;
  text-decoration: underline;
  padding: 5px 0;
}

.table thead tr th {
  font-size: 1.2em;
  font-weight: 800;
}

.table td {
  padding: 3px;
  text-align: left;
}




